import './main.scss';

import ScrollMagic from 'ScrollMagic';
import BScroll from 'better-scroll';
import { PageController } from './js/page-controller';


const pageController = new PageController();
pageController.init();


const $rules = document.querySelectorAll('[id*=rule-]');
const rulesArray = Array.prototype.slice.call( $rules );
const $ruleLine = document.querySelector('[data-rule-line]');
const scrollEvent = () => {

    const scrollController = new ScrollMagic.Controller({
        globalSceneOptions: {
            duration: 0,
            triggerHook: 0.3
        }
    });

    for(let i = 0; i < $rules.length; i++) {
        const $trigger = $rules[i];

        new ScrollMagic.Scene({ triggerElement: $trigger })
            .on('enter', (e) => {
                setRuleColor(e);
            })
            .on('leave', (e) => {
                setRuleColor(e);
            })
            .addTo(scrollController);
    }
}

const setRuleColor = (e) => {
    const $rule = e.target.triggerElement();
    const index = rulesArray.indexOf($rule);
    const modulo = index % 4;
    $ruleLine.setAttribute('data-rule-line', modulo);
}

scrollEvent();


// init better scroll

const wrapper = document.querySelector('.toc__main');
new BScroll(wrapper, {
    click: true
});

new BScroll(document.querySelector('.rule__main'), {
    click: true
});