class PageController {

    constructor() {
        this.$$sectionSwitches = document.querySelectorAll('[data-section-switch]');
        this.$$ruleSwitches = document.querySelectorAll('[data-rule-switch]');
    }

    init() {
        this.bindEvents();
        this.onHashChange();
    }

    bindEvents() {

        window.addEventListener('hashchange', () => {
            this.onHashChange();
        }, false);
    }

    onHashChange() {
        const hash = location.hash,
            id = hash.replace('#', '');
        console.log('hash changed to:' + hash);

        if(hash === '') {
            return false;
        }

        this.handleSection(id);
    }

    handleSection(id) {

        // remove active section class
        const oldActive = document.body.getAttribute('data-active');
        document.body.classList.remove(oldActive + '-active');

        // activate new section class
        const $newSection = document.getElementById(id);
        if($newSection) {
            document.body.classList.add(id + '-active');
            document.body.setAttribute('data-active', id)
        } else {
            console.warn('no secion found with: ' + id);
        }
    }

}

export { PageController }